*{
  margin : 0;
  padding: 0;
  box-sizing: border-box;;
}

/*** HOME PAGE ***/

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #faf0f0;
  font-size: 15px;
  font-family:sans-serif;
  line-height: 140%;
}
.nav{
  padding: 5px;
background-color: #c7d1d5;
}
.nav li{
  display: inline-block;
  margin-left: 10px;
  width: 100px;
  text-align: center;
  color: #b97c0d;
  background-color: #f6e4e4; 
  list-style: none;
}
nav li:hover{
  background-color: #b97c0d;
  color: #ede8e8;
  cursor: pointer;
}
.sign{
  text-align: right;
  padding: 10px;
  background-color: #9e6f6f;
  color: orangered;
height: 300px;
}
.sign-up{
font-size: 18px;
}
.sign-in{
  color: #1de41d;
  margin-top: 10px;
font-size: 16px;
}
.sign a{
  width: 70px;
  height: 30px;
  padding: 5px;
  font-size: 16px;
  background-color: #ede8e8;
  border: 1px solid #ede8e8;
  color: saddlebrown;
  text-decoration: none;
}
.sign a:hover{
  color: #ede8e8;
  background-color: saddlebrown;
}
.sign h3{
margin-right: 120px;
margin-bottom: 20px;
font-size: 20px;
}
.sign-in-btn{
  width: 70px;
  height: 30px;
  padding: 5px;
  font-size: 16px;
  background-color: #ede8e8;
  border: 1px solid #ede8e8;
  color: saddlebrown;
}
.sign-in-btn:hover{
  color: #ede8e8;
  background-color: saddlebrown;
}
.admin-signin{
  width: 70px;
  height: 30px;
  padding: 5px;
  font-size: 16px;
  background-color: #ede8e8;
  border: 1px solid #ede8e8;
  color: saddlebrown;
}
.admin-signin:hover{
  color: #ede8e8;
  background-color: saddlebrown;
}
#myform input{
  width: 250px;
  height: 30px;
  margin-bottom: 10px;
  text-align: left;
  padding: 3px;
  color: brown;
font-size: 16px;
}
input{
  width: 200px;
  height: 25px;
  margin-bottom: 10px;
  text-align: left;
  padding: 3px;
  color: brown;
font-size: 14px;
}
.main-body{
  padding: 10px;
  text-align: center;
}
header h1{
  margin-top: 20px;
  color: #b23737;
}
header h1 emp{
font-style: italic;
background-color: rgba(238, 218, 108, 0.99);
}
header p{
  margin-top: 15px;
  color: #c1ab2e;
  margin-bottom: 20px;
  font-style: italic;
}
img{
  width: 1000px;
}
.info{
  margin-top: 40px;
margin-left: 20%;
  text-align: left;
  color: brown;
}
.info h2{
  margin-bottom: 10px;
}
.info ul{
  margin-left: 20px;
}
#myform label{
  margin-right: 5px;
font-size: 20px;
}

.d-none{
  display: none;
}
.create-acct{
  width: 120px;
  height: 30px;
  font-size: 16px;
  cursor: pointer;
margin-right: 130px;
color: indianred;
margin-top: 20px;
background-color: whitesmoke;
}
.create-acct:hover{
color: whitesmoke;
background-color: indianred;
}

/** SIGNED IN PAGE **/

.nav a{
  text-decoration: none;
  color: #b97c0d;
}
.nav a:hover{
  background-color: #b97c0d;
  color: #ede8e8;
  cursor: pointer;
}
.sign-out{
  float: right;
  clear: both; 
}
.body{
  margin-top: 40px;
  color: #c24343;
}

/** PROFILE **/
.details{
  margin-top: 20px;
}
.details h3,label{
  color: #c1ab2e;
}
.profile-info{
text-align: left;
border: 3px solid lightgray;
width: 500px;
margin: 20px;
background-color:#efe3e3;
}
.profile-info label{
color: #3f3a3a;
}
.profile-info header{
margin-bottom: 70px;
}

/** CREATE ORDER PAGE**/
.body p{
  margin-top: 15px;
}
.body form{
  margin-top: 40px;
  text-align: right;
  padding-right: 40%;
}
.submit-order{
  width: 200px;
  height: 30px;
  cursor: pointer;
}
.username{
  text-transform: capitalize;
}

/** GET ALL ORDERS PAGE **/

button{
  margin-left: 10px;
  width: 120px;
  height: 25px;
  cursor: pointer;
}
.orders a, .details a{
  text-decoration: none;
  color: black;
}
.order-details{
border: 1px solid lightgray;
width: 500px;
margin: 10px auto;
}
.order-info{
text-align: left;
padding: 10px 20%;
}
.location{
  width: 200px;
}
.present-location{
  color: cadetblue;
text-transform: capitalize;
}
.red{
  color: red;
  font-style: italic;
}
.orders{
  display: block;
}

/** ADMIN PAGE **/
.admin{
  float: right;
}
.admin a{
  color: lightcoral;
}
.status-state{
  font-style: italic;
  color: green;
}
.pending{
  color: red;
}
.delivered{
  color: green;
}
.search-by-user{
margin-top: 10px;
}
.search-by-user label{
font-style: italic;
color: cadetblue;
font-size: 14px;
}
.search-by-user input{
height: 25px;
padding: 5px;
}
.admin-profile{
text-align: left;
border: 3px solid lightgray;
width: 500px;
margin: 20px auto;
background-color:#efe3e3;
padding: 10px;
}
.admin-profile label{
color: #3f3a3a;
}

